import { nowActions, mapActions } from './constants';
import { debounce } from 'lodash';

const actionHandlers = {
	[nowActions.NOW_TYPEAHEAD_SELECTED_ITEM_SET]: {
		effect: ({ dispatch, updateState, action: { payload } }) => {
			if (payload && payload.item) {
				updateState({ selectedItem: payload.item });
				if (payload.item.place_id) { // google map location
					dispatch(mapActions.TRIGGER_MAP_ACTION, { action: 'focus', value: payload.item.place_id });
				} else if (payload.item.layer) { // layer record
					dispatch(mapActions.TRIGGER_MARKER_ACTION, { action: 'focus', ...payload.item, record: payload.item.id });
				}
			}
		},
		stopPropagation: true
	},
	[nowActions.NOW_TYPEAHEAD_VALUE_SET]: {
		effect: debounce(({ dispatch, updateState, action: { payload } }) => {
			if (payload.value && payload.value.length > 2)
				dispatch(mapActions.TRIGGER_LOCATION_SEARCH, { searchKeyword: payload.value });
			updateState({ searchKeyword: payload.value });
		}, 500),
		stopPropagation: true
	},
	[nowActions.NOW_BUTTON_ICONIC_CLICKED]: {
		effect: ({ state: { selectedItem }, dispatch, action: { payload }, updateState }) => {
			if (payload.type == 'focus-item') {
				if (selectedItem) {
					if (selectedItem.place_id) { // google map location
						dispatch(mapActions.TRIGGER_MAP_ACTION, { action: 'focus', value: selectedItem.place_id });
					} else if (selectedItem.layer) { // layer record
						dispatch(mapActions.TRIGGER_MARKER_ACTION, { action: 'focus', ...selectedItem, record: selectedItem.id });
					}
				}
			}
		},
		stopPropagation: true
	}
};

export {
	actionHandlers
};