const updateAlertImpactQuery = `
mutation ($id: String!, $action: String!, $impact_radius: Float, $impact_area: String) {
  snBcmMap {
    bcmCrisisMapSchema {
      updateAlertImpact(id: $id, action: $action, impact_radius: $impact_radius, impact_area: $impact_area){
        id
        action
        impact_radius
        impact_area
        impact_type
        impact_state
        resources {
          config_id
          table
          name
          count
          icon
          query
          contact
        }
      }
    }
  }
}
`;

export default updateAlertImpactQuery;