
import { customActions } from './constants';
import { actionTypes } from '@servicenow/ui-core';
import * as data from './handlers/dataHandler';
import * as query from './handlers/queryHandler';

export default {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: {
		effect: ({ state: { properties: { loadLayer } }, dispatch }) => {
			if(loadLayer)
				dispatch(customActions.FETCH_LAYER_DATA);
		},
		stopPropagation: true
	},
	[customActions.FETCH_LAYER_DATA]: query.fetchLayersData,
	[customActions.FETCH_LAYER_DATA_SUCCESS]: {
		effect: data.fetchLayerDataSuccess,
		stopPropagation: true
	},
	[customActions.FETCH_LAYER_DATA_ERROR]: {
		effect: data.errorHandler,
		stopPropagation: true
	}
};