import { t } from 'sn-translate';

const no_tasks_url = require('../assets/no_task.png');

const _getFieldsKeyValue = (fields) => {
    const fieldList = [];
    fields.map(f => {
        let field = {
            'label': f.label,
            'value': {
                'type': f.reference ? 'text-link' : 'string',
                'value': f.reference ? f.value || '-' : f.display_value || '-',
                'label': f.display_value || '-',
                'href': '/' + f.reference + '.do?sys_id=' + f.value,
                'showIcon': false,
                'status': f.value
            }
        };
        fieldList.push(field);
    });
    return fieldList;
};

export const ResponseTasks = ({ state }) => {
    const { responseTasksLoader, alertRelData } = state;
    return (
        <div slot="content" className="response-tasks">
            {
                responseTasksLoader ? <div className="accordion-item-loader"><now-loader /></div> : alertRelData.tasks && alertRelData.tasks.length ? alertRelData.tasks.map((task) => {
                    return (
                        <now-card size="lg"
                            className="clickable-card"
                            append-to-payload={{ type: 'tasklist', task }} interaction="click">
                            <now-card-header
                                tagline={{ 'label': task.number }}
                                heading={{ label: task.short_description, lines: 1, size: 'sm', variant: 'secondary' }}>
                            </now-card-header>
                            <div className="response-fields">
                                <now-label-value-stacked items={_getFieldsKeyValue(task.fields)}
                                    align="horizontal-equal" size="sm"
                                    itemMinWidth="90px" delimiter="," wrapped={true} truncated={true}>
                                </now-label-value-stacked>
                            </div>

                            <now-card-footer size="md" split="equal">
                                <now-text-link slot="end" label={task.name}
                                    href="javascript:void(0)" variant="primary" className="text-sm"
                                    append-to-payload={{ type: 'tasklist', task }}
                                >
                                </now-text-link>
                            </now-card-footer>
                        </now-card>
                    )

                }) : <div className="no-record-content">
                        <img alt={t('No task image')} src={no_tasks_url}></img>
                        <p className="no-record-title">
                            {t('No Related Tasks')}
                        </p>
                        <p className="no-record-desc">
                            {t('No tasks have been started for this alert.')}
                        </p>
                    </div>
            }
        </div>
    )
}