import '@servicenow/now-heading';
import '@servicenow/now-input';
import '@servicenow/now-highlighted-value';
import '@servicenow/now-card';
import '@servicenow/now-label-value';
import '@servicenow/now-button';
import '@servicenow/now-loader';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-badge';
import { mapActions, customActions, keywords } from '../constants';
import { t } from 'sn-translate';
import emptyStateSvg from '../assets/empty-alert-image.svg';

const getFilteredAlerts = (alertData, searchTerm, alertDataCount) => {
	if (alertData && alertData.length && searchTerm) {
		searchTerm = searchTerm.toLowerCase();
		const searchTermAry = searchTerm.split(/\s+/);
		let filteredData = [];
		let count = 0;
		for (let a in alertData) {
			const rec = alertData[a];
			let found = 0;
			for (let str in searchTermAry) {
				const searchStr = searchTermAry[str];
				if ((rec.title && rec.title.toLowerCase().includes(searchStr)) || (rec.source && rec.source.toLowerCase().includes(searchStr)) || (rec.description && rec.description.toLowerCase().includes(searchStr)))
					found++;
				else if (rec.severity && rec.severity.display && rec.severity.display.toLowerCase().includes(searchStr))
					found++;
				else if (rec.fields && rec.fields.length && rec.fields.some((f) => f.display_value && f.display_value.toLowerCase().includes(searchStr)))
					found++;
			}
			if (found == searchTermAry.length) {
				filteredData.push(rec);
				count++;
				if (count > alertDataCount)
					break;
			}
		}
		return filteredData;
	} else
		return alertData && alertData.length ? alertData.slice(0, alertDataCount) : alertData;
};

const getMenuOptions = (canWrite, isActive) => {
	var items = [{ id: 'open_alert', label: t('Open Alert') }];
	if (canWrite) {
		if (isActive == "true")
			items.push({ id: 'dismiss_alert', label: t('Dismiss Alert') });
		else
			items.push({ id: 'reopen_alert', label: t('Reopen Alert') });
	}

	return [{
		id: 'card_menu',
		label: t('Alert Menu'),
		items,
		icon: 'ellipsis-v-outline'
	}];
};

const getAlertActions = (rec) => {
	const actionArr = [];
	if (rec.subscribed)
		actionArr.push({
			id: 'subscribe_alert',
			icon: rec.subscribed == "true" ? 'bell-slash-outline' : 'bell-outline',
			disabled: !rec.can_subscribe,
			label: rec.subscribed == "true" ? t('Unsubscribe') : t('Subscribe')
		});
	return actionArr;
}

const AlertCards = ({ data, searchTerm, mapLayers, alertListLoader, dispatch, alertDataCount }) => {
	const alertData = getFilteredAlerts(data, searchTerm, alertDataCount);
	return alertListLoader || !alertData || (!mapLayers && alertData.length != 0) || (!mapLayers[keywords.ALERT_LAYER_KEY] && alertData.length != 0) ? <div className="pane-item-loader"><now-loader /></div> :
		alertData && alertData.length ? alertData.map((rec) => {
			return (
				<now-card size="lg"
					append-to-payload={{ type: 'alertlist', alert: rec }}
					configAria={{ 'aria-label': t('Open alert of severity {0}', rec.severity.display) + t(' alert headline: ') + rec.title }}
					className="clickable-card"
					on-mouseenter={() => { dispatch(mapActions.TRIGGER_MARKER_ACTION, { action: 'hover', type: 'in', layer: keywords.ALERT_LAYER_ID, collection: keywords.ALERT_COLLECTION_ID, record: rec.id }); }}
					on-mouseleave={() => { dispatch(mapActions.TRIGGER_MARKER_ACTION, { action: 'hover', type: 'out', layer: keywords.ALERT_LAYER_ID, collection: keywords.ALERT_COLLECTION_ID, record: rec.id }); }}
					interaction="click"
					sidebar={{ color: rec.severity.color, variant: 'secondary' }}>
					<now-card-header
						tagline={{ label: rec.source || t("N/A") }}
						heading={rec.title ? { label: rec.title, lines: 1, size: 'sm', variant: 'secondary' } : {}}
						dropdowns={getMenuOptions(rec.can_write, rec.active)}
						actions={getAlertActions(rec)}
						append-to-payload={{ type: 'alertlist', alert: rec }}
					>
						{
							rec.response_task_count ? <now-highlighted-value slot="metadata" label={rec.response_task_count == 1 ? t('{0} Response Task', rec.response_task_count) : t('{0} Response Tasks', rec.response_task_count)} color="blue" variant="tertiary" icon="">
							</now-highlighted-value> : null
						}
					</now-card-header>
					<p className="alertlist-description">{rec.description ? rec.description : rec.title}</p>
					<now-card-footer size="md" split="equal">
						{rec.severity.value ? <now-highlighted-value slot="start" label={rec.severity.display} color={rec.severity.color} variant="tertiary" showIcon={true} icon="">
						</now-highlighted-value> : null}
						{rec.fields && rec.fields[0] ?
							<now-label-value-inline slot="end"
								label={rec.fields[0].label}
								value={rec.fields[0].display_value}
							/>
							: ""}
					</now-card-footer>
				</now-card>
			);
		}) :
			<div className="alert-empty-state">
				<div className="alert-empty-state-img">
					<img alt={t('No items to display.')} src={emptyStateSvg} />
				</div>
				<div className="alert-empty-state-text">
					<h3>{t('No records to display.')}</h3>
				</div>
			</div>
};

export const AlertList = ({ state: { alertData, activeView, searchTerm, alertDetailLoader, alertListLoader, sortColumns, alertActiveDropdownState, activeSortObj, dismissSortObj, alertDataCount, properties: { alertConfig, mapLayers } }, dispatch, updateState }) => {
	return (
		<div className={(activeView == 'list') ? 'full-height' : 'hidden'} >
			<now-loader className={alertDetailLoader ? 'pane-item-loader' : 'hidden'} />
			<div className={`drop ${alertListLoader ? 'alert-loading' : 'alert-list'} ${alertDetailLoader ? 'hidden' : ''}`}>
				<div className="alert-list-top-section">
					<div className="item-header">
						<h1>{t('Alerts')}</h1>
					</div>
					<div className="search-input-box">
						<div className="search-input-box-inner">
							<now-input type="text" placeholder={t("Search")}
								style={{ width: '100%', border: 'none', margin: '0' }}
								aria-label={t('type search string to filter the active alerts list')}
								append-to-payload={{ type: 'search' }}>
								<now-button-iconic slot="start" icon="magnifying-glass-fill" variant="tertiary"
									tooltipContent={t('Search Alerts')}
									append-to-payload={{ id: 'search', type: 'start_search' }} bare />
							</now-input>
						</div>
					</div>

					<div className="alert-list-options">
						<now-label-value-stacked items={[
							{
								'label': t('Show'), 'value': {
									'type': 'jsx', 'value': (<now-dropdown items={[{ 'id': 'active', 'label': t('Active Alerts') },
									{ 'id': 'dismiss', 'label': t('Dismissed Alerts') }]}
										bare={true} append-to-payload={{ type: 'activeAlerts' }}
										select="single" variant="tertiary" size="sm"
										selectedItems={[alertActiveDropdownState]}
										tooltipContent={t('Select active or dismissed alerts')} panelFitProps={{}}
										configAria={{ 'aria-label': t('Select active or dismissed alerts') }} search="none">

									</now-dropdown>)
								}
							}]}

							align="horizontal-equal" size="md" itemMinWidth="" delimiter=",">

						</now-label-value-stacked>

						<span className="options-right">
							{sortColumns && sortColumns.length ? <Fragment>
								<now-label-value-stacked items={[{
									'label': t('Sort by'), 'value': {
										'type': 'jsx', 'value': (
											<now-dropdown items={sortColumns}
												bare={true}
												append-to-payload={{ type: 'alertSort', alertSortDropdown: true }}
												selectedItems={alertActiveDropdownState == 'active' && activeSortObj && activeSortObj.sortField ? [activeSortObj.sortField] : dismissSortObj && dismissSortObj.sortField ? [dismissSortObj.sortField] : [alertConfig.sort_by]}
												select="single" placeholder="" icon="" variant="tertiary" size="sm"
												tooltipContent={t('Select column to sort alerts by')} panelFitProps={{}} configAria={{ 'aria-label': t('Select column to sort alerts by') }} search="none">

											</now-dropdown>
										)
									}
								}]}

									align="horizontal-equal" size="md" itemMinWidth="" delimiter=",">

								</now-label-value-stacked>
								<now-button-bare append-to-payload={{ type: 'alertsortorder' }}
									aria-label={t('change the sort order')} variant="secondary"
									title={t('Change sort order')}
									icon-start={alertActiveDropdownState == 'active' ? activeSortObj.sortOrder == 'desc' ? 'sort-descending-fill' : 'sort-ascending-fill' : dismissSortObj.sortOrder == 'desc' ? 'sort-descending-fill' : 'sort-ascending-fill'}>
								</now-button-bare>
							</Fragment> : <Fragment />}
							<now-button-bare on-click={() => dispatch(customActions.ALERT_REFRESH_TRIGGERED, { action: { id: 'alert_refresh' } })}
								title={t('Refresh Alerts')}
								aria-label={t('refresh the active alerts list')} variant="secondary" icon-start="sync-outline">
							</now-button-bare>
						</span>
					</div>
				</div>
				<div className="card-scrollbox" on-scroll={(e, { elm }) => { if ( !alertListLoader && (elm.scrollTop + elm.offsetHeight) == elm.scrollHeight) updateState({ alertDataCount: alertDataCount + 50 }) }}>
					<AlertCards data={alertData} searchTerm={searchTerm} mapLayers={mapLayers} alertListLoader={alertListLoader} dispatch={dispatch} alertDataCount={alertDataCount} />
				</div>
			</div>
		</div>
	);
};