import './components';
import 'sn-fam-map';
import '@servicenow/now-alert';

export default (state) => {
	const { mapKey, mapClient, mapProperties, mapSearchPlaces, mapLanguage, selectedAlertId, editMode, notifications, properties: { alertConfig }, slotStyles, sidePanelOpened } = state
	return (
		<div className="crisis-map">
			<sn-fam-map loadMap={ mapProperties ? true: false } mapKey={mapKey} mapClient={mapClient} mapSearchPlaces={mapSearchPlaces} mapLanguage={mapLanguage} mapProperties={mapProperties} sidepanel-width="500px">
				<div className="side-panel-slot" slot="side_panel">
					<div className={sidePanelOpened ? 'hidden': 'full-height'}>
					<crisis-alert-view-control component-id="cirsis-alert-view-dataprovider" alertConfig={alertConfig} selectedAlertId={selectedAlertId} editMode={editMode} mapLayers="@fam-map-dataprovider/layers" updatedArea="@fam-map-dataprovider/shape"></crisis-alert-view-control>
					</div>
				</div>
				<div slot="top_right">
					<crisis-search-control searchResults="@fam-map-dataprovider/searchResults" mapSearchPlaces={mapSearchPlaces} layerName="@bcm-crisis-layer-control-dataprovider/layerName"></crisis-search-control>
				</div>
				<div slot="right_top">
					<div className="controls">
						<div className="zoom-controls">
							<crisis-zoom-reset-control selectedAlertId={selectedAlertId}></crisis-zoom-reset-control>
						</div>
						<crisis-layer-control loadLayer={ mapProperties ? true: false } component-id="bcm-crisis-layer-control-dataprovider"/>

					</div>
				</div>
				<div slot="right_center">
					{editMode ? <crisis-alert-resize-control editMode={editMode} selectedAlertId={selectedAlertId} /> : null}
				</div>
			</sn-fam-map>
			{
				notifications && notifications.length ?
					<div className="now-alert-container">
						<now-alert-list items={notifications} configAriaLive="polite" ></now-alert-list>
					</div> : <div />
			}
		</div>
	);
};



