const updateAlertStateQuery = `
    mutation ($id: String!, $active: String!) {
        GlideRecord_Mutation {
            update_sn_fam_alert(sys_id: $id, active: $active) {
                sys_id {
                    value
                }
                active {
                    value
                }
            }
        }
    }
`;
export default updateAlertStateQuery;