import { AlertList } from './templates/alertList';
import { AlertDetail } from './templates/alertDetail';

export default (state, { dispatch, updateState }) => {
	return (
		<main className="main">
			<AlertList state={state} updateState={updateState} dispatch={dispatch} />
			<AlertDetail feed={state.selectedItem} state={state} updateState={updateState} dispatch={dispatch} />
		</main>
	);
};

