import '@servicenow/now-icon';
import '@servicenow/now-card';
import { t } from 'sn-translate';

const no_activity_url = require('../assets/bcm_plan_no_activity.jpg');

export const AssetsAtRisk = ({ state: { assetsAtRiskLoader, alertRelData } }) => {
    return (
        <div slot="content" className="asset-at-risk-container">
            { assetsAtRiskLoader ? <div className="accordion-item-loader"><now-loader /></div> :
                <div className="asset-at-risk">
                    {alertRelData && alertRelData.resources && alertRelData.resources.length ?
                        alertRelData.resources.map((asset) => {
                            return (
                                <now-card size="sm" className="res-at-risk-card" interaction="click" append-to-payload={{ type: "assetlist", asset }}>
                                    <div className="res-at-risk-item">
                                        <div className="res-at-risk-label">
                                            <now-icon slot="metadata" icon={asset.icon || 'map-pin-fill'} size="md" />
                                            <span className="res-at-risk-name">{asset.name}</span>
                                        </div>
                                        <now-badge variant="primary" size="md" status="info" value={asset.count} round></now-badge>
                                    </div>
                                </now-card>
                            )
                        })
                        :
                        <div className="no-record-content">
                            <img className="no-asset-img" alt={t('No assets at risk')} src={no_activity_url}></img>
                            <p className="no-record-title">
                                {t('No assets at risk')}
                            </p>
                            <p className="no-record-desc">
                                {t('No assets found inside the alert impact area.')}
                            </p>
                        </div>
                    }
                </div>
            }
        </div>
    );
};




