
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { customActions } from '../constants';
import languageQuery from '../queries/languageQuery';
import locationQuery from '../queries/locationQuery';

export const fetchUserLanguage = createGraphQLEffect(languageQuery, {
    variableList: [],
    successActionType: customActions.FETCH_USER_LANG_SUCCESS,
    errorActionType: customActions.FETCH_USER_LANG_ERROR
});

export const fetchInitData = createGraphQLEffect(locationQuery, {
    variableList: ['id'],
    successActionType: customActions.FETCH_INIT_DATA_SUCCESS,
    errorActionType: customActions.FETCH_INIT_DATA_ERROR
});
