import { createCustomElement } from '@servicenow/ui-core';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import { nowActions, mapActions, crisisActions, keywords } from '../../constants';
import styles from './crisis-zoom-reset-control.scss';
import { t } from 'sn-translate';

const view = ({ properties: { selectedAlertId } }) => {
	return (
		<div>
			<div className="reset">
				<now-button icon="expand-outline"
					tooltipContent={t('Reset')} variant="tertiary" size="md" append-to-payload={{ action: 'reset', selectedAlertId }} configAria={{ 'button': { 'aria-label': t('Reset Map Zoom') } }}>
				</now-button>
			</div>
			<div className="zoom-control">
				<div className="zoom-in">
					<now-button icon="magnifying-glass-plus-outline"
						tooltipContent={t('Zoom-In')} variant="tertiary" size="md" append-to-payload={{ action: 'zoom_in' }} configAria={{ 'button': { 'aria-label': t('Zoom In') } }}>
					</now-button>
				</div>
				<div className="zoom-out">
					<now-button icon="magnifying-glass-minus-outline"
						tooltipContent={t('Zoom-Out')} variant="tertiary" size="md" append-to-payload={{ action: 'zoom_out' }} configAria={{ 'button': { 'aria-label': t('Zoom Out') } }}>
					</now-button>
				</div>
			</div>
		</div>
	);
};

const actionHandlers = {
	[nowActions.NOW_BUTTON_CLICKED]: {
		effect: ({ dispatch, action: { payload } }) => {
			if (payload.action === 'reset') {
				if (payload.selectedAlertId) {
					dispatch(mapActions.TRIGGER_MARKER_ACTION, { action: 'reset', layer: keywords.ALERT_LAYER_ID, collection: keywords.ALERT_COLLECTION_ID, record: payload.selectedAlertId });
				} else {
					dispatch(mapActions.TRIGGER_MAP_ACTION, payload);
				}
			} else {
				dispatch(mapActions.TRIGGER_MAP_ACTION, payload);
			}
		},
		stopPropagation: true
	}
};

createCustomElement('crisis-zoom-reset-control', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		selectedAlertId: ''
	},
	initialState: {
	},
	styles,
	actionHandlers
});