import { createCustomElement } from '@servicenow/ui-core';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import styles from './crisis-search-control.scss';
import view from './view';
import { actionHandlers } from './actions';

createCustomElement('crisis-search-control', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		searchResults: {
			default: []
		},
		mapSearchPlaces: {
			default: false
		},
		layerName: {
			default: ""
		}
	},
	initialState: {
		searchKeyword: undefined,
		selectedItem: {}
	},
	styles,
	actionHandlers
});