const alertRelatedDataQuery = `
query ($id:String!) {
  snBcmMap {
    bcmCrisisMapSchema {
      getAlertRelatedData(id: $id) {
        resources {
            config_id
            table
            name
            count
            icon
            query
            contact
          }
          actions{
            id
            name
            title
            description
            run_once
            action_name
            data{
              name
              value
            }
          }
          tasks{
            table
            sys_id
            number
            short_description
            fields {
              name
              value
              display_value
              label
              reference
            }
          }
      }
    }
  }
}
  `;

export default alertRelatedDataQuery;