
import { customActions, nowActions, crisisActions, mapActions, keywords } from './constants';
import { actionTypes } from '@servicenow/ui-core';
import * as data from './handlers/dataHandler';
import * as query from './handlers/queryHandler';

export default {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: {
		effect: data.componentBootStrapHandler,
		stopPropagation: true
	},
	[actionTypes.COMPONENT_DOM_TREE_READY]: {
		effect: ({ state, dispatch }) => {
			if (!state.selectedAlertId) {
				dispatch(nowActions.CONTENT_UPDATED, { params: { 'alert': 'list' } });
			}
		},
		stopPropagation: true
	},
	[customActions.FETCH_INIT_DATA]: query.fetchInitData,
	[customActions.FETCH_INIT_DATA_SUCCESS]: {
		effect: data.initDataSuccessHandler,
		stopPropagation: true
	},
	[customActions.FETCH_INIT_DATA_ERROR]: {
		effect: data.initDataErrorHandler,
		stopPropagation: true
	},
	[customActions.FETCH_USER_LANG]: query.fetchUserLanguage,
	[customActions.FETCH_USER_LANG_SUCCESS]: {
		effect: data.userLanguageSuccessHandler,
		stopPropagation: true
	},
	[customActions.FETCH_USER_LANG_ERROR]: {
		effect: data.userLanguageErrorHandler,
		stopPropagation: true
	},
	[crisisActions.NOTIFICATION_MSG_RECEIVED]: {
		effect: data.notificationMsgReceivedHandler,
		stopPropagation: true
	},
	[nowActions.NOW_ALERT_LIST_ITEMS_SET]: {
		effect: data.notificationMsgDismissHandler,
		stopPropagation: true
	},
	[crisisActions.EDIT_MODE_SET]: {
		effect: ({ action: { payload: { enable } }, updateState }) => {
			updateState({
				editMode: enable,
			});
		},
		stopPropagation: true
	},
	[crisisActions.SET_SELECTED_ALERT_ID]: {
		effect: data.updateSelectedAlertId,
		stopPropagation: true
	},
	[mapActions.MARKER_SELECTED]: {
		effect: data.updateSelectedAlertId,
		stopPropagation: true
	},
	[mapActions.SIDE_PANEL_OPENED] : {
		effect: ({ action: { payload: { value } }, updateState }) => {
			updateState({ sidePanelOpened: value });
		},
		stopPropagation: true
	}
};