import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-modal';
import { find } from 'lodash';
import { t } from 'sn-translate';

export const CrisisActionModel = ({ state }) => {
    const { selectedAlert, crisisModal, invalidModalInputs, alertRelData: { actions, resources } } = state;
    let crisisShortDescriptionLabel = t('Short Description');
    let crisisDescriptionLabel = t('Description');
    let crisisBcmResArr = [];
    const declareCrisisActionInfo = find(actions, {action_name: 'declare_crisis'});
    const declareCrisisData = declareCrisisActionInfo ? declareCrisisActionInfo.data : null;
    if (declareCrisisData) {
        const cisisShortDescriptionInfo = find(declareCrisisData, { name: 'short_description' });
        if (cisisShortDescriptionInfo)
            crisisShortDescriptionLabel = cisisShortDescriptionInfo.value;
        
        const cisisDescriptionInfo = find(declareCrisisData, { name: 'description' });
        if (cisisDescriptionInfo)
            crisisDescriptionLabel = cisisDescriptionInfo.value;
        
        const resInfo = find(declareCrisisData, { name: 'resources' });
        if (resInfo)
            crisisBcmResArr = resInfo.value.split(',');
    }
    return <Fragment>
        <now-modal size="lg" headerLabel={t('Declare a Crisis Event')} opened={crisisModal == 'start'}
            append-to-payload={{ type: 'crisis' }}
            footerActions={[
                { label: t('Next'), variant: 'primary', id: 'next', type: 'crisis' },
                { label: t('Cancel'), variant: 'secondary', id: 'cancel', type: 'crisis' }
            ]}
        >
            <div className="crisis-modal">
                <h4> {t('Step 1 - Event Description')} </h4>
                <div className="small p-b-sm">
                    {t('Provide a brief description of the event to get started')}
                </div>

                <now-input label={crisisShortDescriptionLabel} className="full-width"
                    value={selectedAlert.title}
                    append-to-payload={{ type: 'crisis' }}
                    name="short_description" messages={[]} placeholder="" invalid={invalidModalInputs.cri_short_description}
                    required={true} step="any" type="text">
                </now-input>
                <now-input label={crisisDescriptionLabel} className="full-width"
                    name="description" messages={[]} placeholder="" invalid={invalidModalInputs.cri_description}
                    append-to-payload={{ type: 'crisis' }}
                    required={true} step="any" type="text">
                </now-input>
            </div>
        </now-modal>
        <now-modal size="lg" headerLabel={t('Declare a Crisis Event')} opened={crisisModal == 'next'}
            append-to-payload={{ type: 'crisis' }}
            footerActions={[
                { label: t('Start Crisis Event'), variant: 'primary', id: 'submit', type: "crisis" },
                { label: t('Back'), variant: 'secondary', id: 'back', type: "crisis" }
            ]}
        >
            <div className="crisis-modal">
                <h4> {t('Assets at Risk')} </h4>
                {resources.length ?
                    <div>
                        <div className="small">
                            {t('Select the assets at risk to include in the crises event')}
                        </div>
                        <div className="checkbox-container">
                            {
                                resources.map((asset) => {
                                    const isAssetMapped = crisisBcmResArr.indexOf(asset.config_id) > -1;
                                    return (
                                        <now-checkbox className="checkbox" append-to-payload={{ type: 'crisis' }} disabled={!isAssetMapped} checked={isAssetMapped} configAria={{}} name={asset.config_id} label={asset.name}></now-checkbox>
                                    )
                                })
                            }
                        </div>
                        <div className="small grey"> {t('Note: Disabled assets needs to be mapped to element definition.')} </div>
                    </div>
                    : <div className="small">
                        {t('No assets are at risk, crisis event will be created without any element definition mapped')}
                    </div>
                }
            </div>
        </now-modal>
    </Fragment>
};