import '@servicenow/now-typeahead';
import '@servicenow/now-button';

import { t } from 'sn-translate';

export default ({ searchKeyword, selectedItem, properties: { searchResults, mapSearchPlaces, layerName } }) => {
	return (
		<div className="search-container">
			<now-typeahead items={searchResults}
				value={searchKeyword}
				manageValue={true}
				selectedSearchItem={selectedItem ? selectedItem.id: null}
				placeholder={mapSearchPlaces ? t('Search {0} and Locations', layerName) : t('Search {0}', layerName)} append-to-payload={{ type: 'search' }} >
				<now-button-iconic
					slot="end"
					icon="magnifying-glass-outline"
					variant="tertiary"
					bare
					append-to-payload={{ type: 'focus-item' }}
					tooltip-content={t('Search selected item on map')}
					config-aria={{
						'aria-label': t('Search selected item on map')
					}}
				/>
			</now-typeahead>
		</div>
	);
};
