import { mapActions } from '../constants';
import { get } from 'lodash';

export const fetchLayerDataSuccess = ({ action: { payload }, updateState, dispatch, updateProperties }) => {
	const data = get(payload.data, 'snBcmMap.bcmCrisisMapSchema.getAssets');
	if (data && data.active && data.collections && data.collections.length) {
		dispatch(mapActions.ADD_LAYER, data);
		var layer = {};
		layer.name = data.name;
		layer.id = data.id;
		layer.collections = [];
		data.collections.map(col => {
			if (col.active && col.records && col.records.length) {
				layer.collections.push({
					name: col.name,
					id: col.id,
					icon: col.icon,
					active: col.active
				});
			}
		});
		updateState({ layer });
		updateProperties({ layerName: layer.name || 'Assets' })
	}
};

export const errorHandler = ({ action }) => {
	console.log('Error - ', action.meta, action.payload);
};