import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { customActions } from '../constants';
import layerDataQuery from '../graphql/layerDataQuery';

export const fetchLayersData = createGraphQLEffect(layerDataQuery, {
	variableList: [],
	successActionType: customActions.FETCH_LAYER_DATA_SUCCESS,
	errorActionType: customActions.FETCH_LAYER_DATA_ERROR
});