import { createCustomElement } from '@servicenow/ui-core';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import styles from './crisis-layer-control.scss';
import actionHandlers from './actions';
import '@servicenow/now-popover';
import '@servicenow/now-button';
import { t } from 'sn-translate';
import './int-layer-details';
import { customActions } from './constants';

const view = ({ layer }) => {
	return (
		layer && layer.collections && layer.collections.length ?
			<div className="layer-control">
				<now-popover positions={[{ target: 'top-start', content: 'top-end' }]}>
					<now-button icon="menu-outline" tooltipContent={t('Layers')} slot="trigger" variant="tertiary" size="md" configAria={{ 'button': { 'aria-label': t('Layer toggle menu') } }}></now-button>
					<int-layer-details layer={layer} slot="content" />
				</now-popover>
			</div> : null
	);
};

createCustomElement('crisis-layer-control', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		layer: {},
	},
	properties: {
		layerName: {
			default: "",
			selectable: true
		},
		loadLayer: {
			default: true,
			onChange(currentValue, previousValue, dispatch) {
				if (currentValue)
					dispatch(customActions.FETCH_LAYER_DATA);
			}
		}
	},
	styles,
	actionHandlers
});