import { customActions } from '../constants';
import { getProperty } from 'sn-uxpage-presource';
import { get } from 'lodash';

export const componentBootStrapHandler = ({ state: { properties: { mapProperties } }, action: { payload: { host } }, updateState, dispatch }) => {
	let hasLocation = false;
	if (mapProperties && mapProperties.length) {
		const centerProp = mapProperties.find(prop => {
			return prop.name === 'center';
		});
		if (centerProp && centerProp.value && !(centerProp.value.lat && centerProp.value.lng) && centerProp.value.location) {
			dispatch(customActions.FETCH_INIT_DATA, { id: centerProp.value.location });
			hasLocation = true;
		}
	}
	if (!hasLocation)
		dispatch(customActions.FETCH_USER_LANG);

	const value = host && host.alert;
	if (value && value !== 'list') {
		updateState({
			selectedAlertId: value
		});
	}
};

export const initDataSuccessHandler = ({ action: { payload }, updateState }) => {
	let languageData = 'en';
	let location = null;
	if (payload.data) {
		const locationData = get(payload.data, 'GlideRecord_Query.cmn_location._results');
		languageData = get(payload.data, 'GlideDomain_Query.user.language');
		if (locationData && locationData.length) {
			const { latitude: { value: lat }, longitude: { value: lng }, sys_id: { value: sysId } } = locationData[0];
			if (lat && lng) {
				location = { lat, lng, sysId }
			}
		}
	}
	setMapProperties(languageData, location, updateState);
};

export const userLanguageSuccessHandler = ({ action: { payload }, updateState }) => {
	let languageData = 'en';
	if (payload.data)
		languageData = get(payload.data, 'GlideDomain_Query.user.language');
	setMapProperties(languageData, null, updateState);
};

export const userLanguageErrorHandler = ({ action: { payload }, updateState }) => {
	console.log('Error - ', payload);
	setMapProperties(['en'], null, updateState);
};

export const initDataErrorHandler = ({ action: { payload }, updateState }) => {
	console.log('Error - ', payload);
	setMapProperties('en', null, updateState);
};

export const updateSelectedAlertId = ({ action: { payload: { record } }, updateState }) => {
	updateState({
		selectedAlertId: record,
	});
};

export const notificationMsgReceivedHandler = ({ updateState, action: { payload: { notificationMsg: { status, header, content } } } }) => {
	let icon = "";
	if (status === 'success') {
		icon = 'check-outline';
		status = 'positive';
	} else if (status === 'failed') {
		icon = 'exclamation-triangle-outline';
		status = 'critical';
	}
	//Generating globally unique 10-digit ID for alert notifications
	let notificationMsg = {
		id: Math.floor(Math.random() * 100000000000),
		status,
		icon: icon || 'info-circle-outline',
		header : header + ' : ',
		content: { 'type': 'html', 'value': content },
		action: { 'type': 'dismiss' }
	};
	updateState(
		{
			path: 'notifications',
			value: notificationMsg,
			operation: 'push'
		});
};

export const notificationMsgDismissHandler = ({ updateState, action: { payload: {value} } }) => {
	updateState({
		path: 'notifications',
		value,
		operation: 'set'
	});
};

const setMapProperties = (userPrefLang, location, updateState) => {
	let mapProperties = [{
		name: 'center',
		value: {
			lat: location ? location.lat : '',
			lng: location ? location.lng : '',
			location: location ? location.sysId : ''
		}
	}];
	const props = {
		language: userPrefLang || getProperty('glide.sys.language'),
		method: getProperty('google.maps.method'),
		key: getProperty('google.maps.key'),
		client: getProperty('google.maps.client'),
		places: getProperty('sn_bcm_map.use_google_places_lib')
	};

	updateState({
		mapKey: props.method == 'key' && props.key ? props.key : null,
		mapClient: props.client || null,
		mapLanguage: props.language,
		mapProperties,
		mapSearchPlaces: props.places
	})
};