import '@servicenow/now-card';
import '@servicenow/now-tabs';
import '@servicenow/now-accordion';
import '@servicenow/now-button';
import '@servicenow/now-badge';
import '@servicenow/now-progress-bar';
import '@servicenow/now-loader';
import '@servicenow/now-modal';
import '@servicenow/now-activity-stream';
import isEqual from 'lodash/isEqual';

import { t } from 'sn-translate';

import { AssetsAtRisk } from './assetsAtRisk';
import { ResponseTasks } from './responseTasks';
import { SuggestedActions } from './suggestedActions';

const _getFieldsKeyValue = (fieldInfo) => {
	const infoList = [];
	fieldInfo.map(f => {
		const isReference = f.reference ? true : false;
		const isSev = f.label == 'Severity' ? true : false;
		let field = {
			'label': f.label,
			'value': {
				'type': isSev ? 'highlighted-value' : isReference ? 'text-link' : 'string',
				'value': isReference ? f.value : f.display_value ? f.display_value + '' : '-',
				'label': isSev || isReference ? f.display_value ? f.display_value : '-' : '-',
				'href': '/' + f.reference + '.do?sys_id=' + f.value + '&alert_link',
				'showIcon': isSev ? true : false,
				'status': isSev ? f.value : ''
			}
		};
		infoList.push(field);
	});
	return infoList;
};

const getMenuOptions = (alert, editMode) => {
	var items = [{ id: 'open_alert', label: t('Open Alert') }];

	if (alert.can_write) {
		if (alert.active)
			items.push({ id: 'dismiss_alert', label: t('Dismiss Alert') });
		else
			items.push({ id: 'reopen_alert', label: t('Reopen Alert') });
	}
	if (!editMode && alert.can_write && alert.can_edit_impact) {
		items.push({ id: 'edit_mode', label: t('Edit Impact Area') });
		if (alert.impact_state == 'customised')
			items.push({ id: 'revert_area', label: t('Revert Impacted Area') });
	}
	return [{
		id: 'card_menu',
		label: t('Alert Menu'),
		items,
		icon: 'ellipsis-v-outline'
	}];
};

const getAlertActions = (alert) => {
	const actionArr = [];
	if (alert.subscribed)
		actionArr.push({
			id: 'subscribe_alert',
			icon: alert.subscribed == "true" ? 'bell-slash-outline' : 'bell-outline',
			disabled: !alert.can_subscribe,
			label: alert.subscribed == "true" ? t('Unsubscribe') : t('Subscribe')
		});
	return actionArr;
}

const getTabItems = (count = "") => {
	return [
		{ 'id': 'res_at_risk', 'label': t('Details') },
		{ 'id': 'response_tasks', 'label': t('Response Tasks'), count },
		{ 'id': 'history', 'label': t('Activity') }
	];
}

export const AlertDetail = ({ state, updateState }) => {
	const { tabSelected, selectedAlert, alertDetailLoader, alertRelData, properties: { selectedAlertId, editMode }, showMore } = state;
	const isValidAlert = selectedAlert && selectedAlert.id ? true : false;

	const alertFieldsStacked = isValidAlert && selectedAlert.fields_stacked ? _getFieldsKeyValue(selectedAlert.fields_stacked) : null;
	const alertFieldsStackedPrimary = alertFieldsStacked ? alertFieldsStacked.slice(0, 4) : [];
	const alertFieldsStackedSecondary = alertFieldsStacked ? alertFieldsStacked.slice(4, alertFieldsStacked.length + 1) : [];
	const alertFieldsList = isValidAlert && selectedAlert.fields_list ? _getFieldsKeyValue(selectedAlert.fields_list) : null;
	return (
		<div className={ (state && state.activeView == 'detail') ? 'full-height' : 'hidden' }>
			<now-loader className={ alertDetailLoader || !selectedAlert ? 'pane-item-loader' : 'hidden' } />
			<div className={ !(alertDetailLoader || !selectedAlert) ? 'drop alert-detail' : 'hidden' } >
				<div className="card-wrapper">
					<now-card size="sm"
						append-to-payload={{ alert: isValidAlert ? selectedAlert : '' }}>
						<now-card-header
							heading={{ label: '', lines: 1, size: 'md' }}
							dropdowns={isValidAlert ? getMenuOptions(selectedAlert, editMode) : []}
							actions={isValidAlert ? getAlertActions(selectedAlert) : []}
							append-to-payload={{ alert: isValidAlert ? selectedAlert : '' }}
							caption={{
								label: isValidAlert ? selectedAlert.detail : '',
								lines: 1,
								size: 'sm'
							}}> {
								isValidAlert && !selectedAlert.active ? <now-highlighted-value slot="metadata" label={t('Dismissed')} color="low" variant="tertiary" icon="">
								</now-highlighted-value> : null
							}
							<now-button-bare label={t('Back')} variant="secondary" size="md" slot="identifier"
								append-to-payload={{ type: 'backtolist' }}
								iconStart="arrow-left-fill" iconEnd=""
								configAria={{ 'button': { 'aria-label': 'Go Back to alert list' } }}>

							</now-button-bare>
						</now-card-header>
						<slot>
							<now-heading className="alert-heading" label={isValidAlert ? selectedAlert.title : t('No Alerts Found!')} level="1" variant="header-secondary" wontWrap={true} ></now-heading>
						</slot>
						{
							isValidAlert ? <slot>
								<div>
									<now-label-value-stacked className="p-b-md" items={alertFieldsStackedPrimary}
										align="horizontal-equal" size="sm"
										itemMinWidth="90px" delimiter="," wrapped={true} truncated={true}>
									</now-label-value-stacked>
									{!showMore && selectedAlert.fields_list ? <div className="show-more"> <now-button-bare label={t('Show More')} variant="secondary" size="sm" slot="identifier"
										on-click={() => { updateState({ showMore: true }); }}
										iconStart="chevron-down-outline" iconEnd=""
										configAria={{ 'button': { 'aria-label': t('Show More') } }}>

									</now-button-bare></div> : null}
									{showMore && alertFieldsStackedSecondary && alertFieldsStackedSecondary.length ? <now-label-value-stacked
										items={alertFieldsStackedSecondary}
										className="p-b-md"
										align="horizontal-equal" size="sm"
										itemMinWidth="90px" delimiter="," wrapped={true} truncated={true}>
									</now-label-value-stacked> : null
									}
									{showMore && selectedAlert.fields_list ?
										<div>
											{alertFieldsList.map(fieldInfo => {
												return (
													<div className="p-b-md">
														<now-label-value-tabbed size="sm"
															items={[fieldInfo]}
															delimiter=","
														/>
													</div>
												);
											})}

											<div className="show-less">
												<now-button-bare label={t('Show Less')} variant="secondary" size="sm" slot="identifier"
													on-click={() => { updateState({ showMore: false }); }}
													iconStart="chevron-up-outline" iconEnd=""
													configAria={{ 'button': { 'aria-label': t('Show Less') } }}>

												</now-button-bare>
											</div>
										</div> : null
									}
								</div>
							</slot> : null
						}
					</now-card>
				</div>

				{isValidAlert ? <div className="details">
					<div className="tabs">
						<now-tabs items={getTabItems(alertRelData && alertRelData.tasks ? alertRelData.tasks.length : null)}
							selectedItem={tabSelected}
							fixedWidth={true}
						>
						</now-tabs>
						<div className="tab-content scroller">
							<div className={ (tabSelected == 'response_tasks') ? '' : 'hidden' } >
								<now-heading className="response-task-heading" label={t('Response Tasks')} level={6} variant="header-tertiary" hasNoMargin={true}></now-heading>
								<ResponseTasks state={state} />
							</div>
							<div className={ (tabSelected == 'res_at_risk') ? '' : 'hidden' } >
								<now-accordion heading-level="4">
									<now-accordion-item header={{
										label: t('Assets at Risk'),
										size: 'md',
									}} expanded>
										<AssetsAtRisk state={state} />
									</now-accordion-item>
									<now-accordion-item header={{
										label: t('Suggested Actions'),
										size: 'md',
									}} expanded>
										<SuggestedActions state={state} updateState={updateState} />
									</now-accordion-item>
								</now-accordion>
								<div className="hide-accordian-hr"></div>
							</div>
							<div slot="content" className={ (tabSelected == 'history') ? 'activity-stream-container' : 'hidden' }>
								<now-activity-combo
									table="sn_fam_alert"
									sysId={selectedAlertId}
									fields={{
										comments: {
											name: 'comments',
											label: 'Comments',
											sys_readonly: !selectedAlert.can_comment,
											readonly: !selectedAlert.can_comment,
											visible: true,
											type: 'journal_input',
											maxLength: 4000
										}
									}}
								/>
							</div>
						</div>
					</div>
				</div> : null}
			</div>
		</div>
	);
};