import '@servicenow/now-text-link';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import { CommunicationActionModel } from './communicationActionModel';
import { CrisisActionModel } from './crisisActionModel';

const no_tasks_url = require('../assets/no_task.png');

export const SuggestedActions = ({ state, updateState }) => {
	const { selectedAlert, suggestedActionsLoader, executedActions, alertRelData, communicationModal,  crisisModal} = state;
	return (
		<div slot="content" className="suggested-actions">
			{
				suggestedActionsLoader ? <div className="accordion-item-loader"><now-loader /></div> :
					<div>
						{alertRelData && alertRelData.actions && alertRelData.actions.length ? alertRelData.actions.map((action) => {
							return (
								<now-card size="lg"
									className="clickable-card">
									<now-card-header heading={{ label: action.title, lines: 1, size: 'sm', variant: 'secondary' }}>
									</now-card-header>
									<p className="alertlist-description">
										{action.description}
									</p>
									<now-card-footer size="md" split="equal">
										{action.run_once && executedActions.length && executedActions.indexOf(action.id) > -1 ?
											<span className="disabled-link" slot="end">{action.name}</span>
											:
											<now-text-link slot="end" label={action.name}
												variant="primary" className="text-sm"
												href="javascript:void(0)"
												append-to-payload={{ type: "suggested_action", action, selectedAlert }}
											>
											</now-text-link>
										}
									</now-card-footer>
								</now-card>

							)
						}) : <div className="no-record-content">
								<img alt={t('no actions image')} src={no_tasks_url}></img>
								<p className="no-record-title">
									{t('No Suggested Actions')}
								</p>
								<p className="no-record-desc">
									{t('No Suggested Actions configured for this Alert')}
								</p>
							</div>
						}
						{alertRelData && alertRelData.actions && alertRelData.actions.length ? <Fragment>
							{communicationModal ? <CommunicationActionModel state={state} updateState={updateState} /> : null}
							{crisisModal ? <CrisisActionModel state={state} /> : null}
						</Fragment> : null
						}
					</div>
			}
		</div>
	)
};