import { createCustomElement } from '@servicenow/ui-core';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-bcm-crisis-map-connected.scss';
import view from './view';
import actionHandlers from './actions';

const TAG_NAME = 'sn-bcm-crisis-map-connected';

/**
 * Google Map to manage crisis alerts and impacted assets
 * 
 * ```
 * import '@relative-path/app-bcm-crisis-map';
 * 
 * <sn-bcm-crisis-map-connected>
 * 		alert-config=""
 * 		map-properties=""
 * </sn-bcm-crisis-map-connected>
 * ```
 * 
 * @seismicElement sn-bcm-crisis-map-connected
 * @summary An interactive UI to manage ciris alerts and impacted assets with google map 
 */

createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
		 * Configurations to manage alerts
		 * 
		 * @type {JSON}
		 */
		alertConfig: {
			default: {
				condition: 'active=true',
				info_field: 'sys_updated_on',
				icon: 'triangle-exclamation-fill,circle-label',
				sort_fields: ['severity:asc', 'sys_created_on', 'sys_updated_on'],
				sort_by: 'severity',
				sort_order: 'asc',
				view: 'crisis'
			}
		},
		/**
		 * Configurations to manage google map
		 * Accepts most of the google map properties
		 * 
		 * @type {Array}
		 */
		mapProperties: {
			default: [{
				name: 'center',
				value: {
					lat: '',
					lng: '',
					location: ''
				}
			}]
		}
	},
	initialState: {
		/**
		 * Google Map Key
		 * 
		 * @type {String}
		 */
		mapKey: null,
		/**
		 * Google Map client id
		 * 
		 * @type {String}
		 */
		mapClient: null,
		/**
		 * Google Map properties
		 * 
		 * @type {String}
		 */
		mapProperties: null,
		/**
		 * Weather to use google Map places api for search 
		 * 
		 * @type {Boolean}
		 */
		mapSearchPlaces: false,
		/**
		 * Google Map language
		 * 
		 * @type {String}
		 */
		mapLanguage: null,
		notifications: [],
		editMode: false,
		selectedAlertId: '',
		sidePanelOpened: false
	},
	styles,
	actionHandlers
});