import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import updateAlertStateQuery from '../graphql/updateAlertStateQuery';
import alertDetailQuery from '../graphql/alertDetailQuery';
import alertRelatedDataQuery from '../graphql/alertRelatedDataQuery';
import alertListQuery from '../graphql/alertListQuery';
import alertSubscribeQuey from '../graphql/alertSubscribeQuey';
import updateAlertImpactQuery from '../graphql/updateAlertImpactQuery';
import runAlertActionQuery from '../graphql/runAlertActionQuery';
import { customActions } from '../constants';

export const updateAlertStateEffect = createGraphQLEffect(updateAlertStateQuery, {
	variableList: ['id', 'active'],
	successActionType: customActions.UPDATE_ALERT_STATE_SUCCESS,
	errorActionType: customActions.UPDATE_ALERT_STATE_FAILURE
});

export const fetchAlertDetail = createGraphQLEffect(alertDetailQuery, {
	variableList: ['id', 'view'],
	successActionType: customActions.FETCH_ALERT_DETAIL_SUCCESS,
	errorActionType: customActions.FETCH_ALERT_DETAIL_FAILURE
});

export const fetchAlertRelatedData = createGraphQLEffect(alertRelatedDataQuery, {
	variableList: ['id'],
	successActionType: customActions.FETCH_ALERT_RELATED_DATA_SUCCESS,
	errorActionType: customActions.FETCH_ALERT_RELATED_DATA_FAILURE
});

export const fetchAlertList = createGraphQLEffect(alertListQuery, {
	variableList: ['alertConfig'],
	successActionType: customActions.FETCH_ALERT_LIST_SUCCESS,
	errorActionType: customActions.FETCH_ALERT_LIST_FAILURE
});

export const subscribeAlertEffect = createGraphQLEffect(alertSubscribeQuey, {
	variableList: ['id', 'subscribed'],
	successActionType: customActions.SUBSCRIBE_ALERT_SUCCESS,
	errorActionType: customActions.SUBSCRIBE_ALERT_FAILURE
});

export const updateAlertImpact = createGraphQLEffect(updateAlertImpactQuery, {
	variableList: ['id', 'action', 'impact_radius', 'impact_area'],
	successActionType: customActions.UPDATE_ALERT_IMPACT_SUCCESS,
	errorActionType: customActions.UPDATE_ALERT_IMPACT_FAILURE
});

export const runAlertAction = createGraphQLEffect(runAlertActionQuery, {
	variableList: ['action_id', 'alert_id', 'inputs'],
	successActionType: customActions.RUN_ALERT_ACTION_SUCCESS,
	errorActionType: customActions.RUN_ALERT_ACTION_FAILURE
});