import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import { nowActions, mapActions } from './constants';
import styles from './crisis-layer-control.scss';
import { t } from 'sn-translate';

const view = ({ properties: { layer }, activeLayers }) => {
	return <div className="container">
		<h4 className="heading"> {layer.name} </h4>
		{layer.collections && layer.collections.length ? layer.collections.map((c) => {
			const layerIndex = activeLayers.indexOf(layer.id + '_' + c.id);
			return (<div className="layer-item">
				<now-checkbox className="checkbox" checked={layerIndex != -1}
					configAria={{ 'button': { 'aria-label': t('Toggle Layer') } }} label={c.name}
					append-to-payload={{ layer: layer.id, collection: c.id, index: layerIndex }}
				>
				</now-checkbox>
				<now-icon icon={c.icon || 'map-pin-fill'} size="md"></now-icon>
			</div>);
		}) : <div></div>
		}
	</div>
};

export const actionHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: {
		effect: ({ state: { properties: { layer } }, updateState }) => {
			let activeLayers = [];
			if (layer && layer.collections && layer.collections.length) {
				layer.collections.map(c => {
					if (c.active)
						activeLayers.push(layer.id + '_' + c.id);
				});
			}
			if (activeLayers.length)
				updateState({ activeLayers });
		},
		stopPropagation: true
	},
	[nowActions.NOW_CHECKBOX_CHECKED_SET]: {
		effect: ({ dispatch, action: { payload }, updateState }) => {
			if (payload.layer && payload.collection)
				dispatch(mapActions.TOGGLE_LAYER, payload);

			let obj = {};
			obj.path = 'activeLayers';
			if (payload.index != -1) {
				obj.operation = 'splice';
				obj.start = payload.index;
				obj.deleteCount = 1;
			} else {
				obj.operation = 'push';
				obj.value = payload.layer + '_' + payload.collection;
			}
			updateState(obj);
		},
		stopPropagation: true
	}
};

createCustomElement('int-layer-details', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		layer: {
			default: {},
		}
	},
	initialState: {
		activeLayers: []
	},
	styles,
	actionHandlers
});