const alertDetailQuery = `
query ($id:String!, $view: String!) {
  snBcmMap {
    bcmCrisisMapSchema {
      getAlertDetail(id: $id, view:$view) {
        title
        id
        active
        can_comment
        can_write
        can_edit_impact
        impact_area
        impact_radius
        impact_type
        impact_state
        subscribed
        can_subscribe
        fields_stacked {
          name
          value
          display_value
          label
          reference
        }
        fields_list {
          name
          value
          display_value
          label
          reference
        }
      }
    }
  }
}
  `;

export default alertDetailQuery;