import { customActions, mapActions, crisisActions, keywords, translatedText } from '../constants';
import { findIndex, clone, get } from 'lodash';
import { t } from 'sn-translate';

export const fetchAlertDetailSuccess = ({ action: { payload }, updateState, dispatch }) => {
	if (payload.data) {
		const selectedAlert = get(payload.data, 'snBcmMap.bcmCrisisMapSchema.getAlertDetail');

		if (selectedAlert) {
			updateState({ selectedAlert, alertDetailLoader: false });
			dispatch(mapActions.TRIGGER_MARKER_ACTION, { layer: keywords.ALERT_LAYER_ID, collection: keywords.ALERT_COLLECTION_ID, record: selectedAlert.id, recordData: selectedAlert });
			dispatch(customActions.FETCH_ALERT_RELATED_DATA, { id: selectedAlert.id });
			if (selectedAlert.impact_state && selectedAlert.impact_state == "outdated") {
				let notificationMsg = {
					status: 'info',
					header: t(translatedText.MSG_INFO_HEADER_REVERT_IMPACT),
					content: t(translatedText.MSG_INFO_REVERT_IMPACT)
				};
				dispatch(crisisActions.NOTIFICATION_MSG_RECEIVED, { notificationMsg });
			}
		} else
			updateState({ selectedAlert: null, activeView: 'list', alertDetailLoader: false });
	}
};

export const fetchAlertListSuccess = ({ action: { payload }, dispatch, updateState, state: { firstLoadComplete, properties: { selectedAlertId, alertConfig } } }) => {
	if (payload.data) {
		const data = get(payload.data, 'snBcmMap.bcmCrisisMapSchema.getAlertList');

		if (data && (data.records || data.sort_fields)) {
			let newalertData = data.records && data.records.length ? data.records : [];
			if (newalertData) {
				const records = newalertData.map((rec) => {
					return {
						id: rec.id,
						latitude: rec.latitude,
						longitude: rec.longitude,
						table: "sn_fam_alert",
						icon: rec.icon,
						severity: rec.severity,
						title: rec.title
					};
				});

				if (firstLoadComplete) {
					dispatch(mapActions.UPDATE_LAYER, { action: 'replace_records', layer: keywords.ALERT_LAYER_ID, collection: keywords.ALERT_COLLECTION_ID, records, 'reset_bound': true });
				} else {
					dispatch(mapActions.ADD_LAYER,
						{
							'name': 'Alerts',
							'id': keywords.ALERT_LAYER_ID,
							'toggle': false,
							'active': true,
							'show_info': false,
							'click_action': 'select',
							'focus': true,
							'cluster_threshold': 100,
							'collections': [
								{
									'name': 'Alerts',
									'id': keywords.ALERT_COLLECTION_ID,
									'records': records,
									'active': true,
									'toggle': false,
									'skip_search': true
								}
							],
						}
					);
				}
			}

			if (!firstLoadComplete && selectedAlertId) {
				dispatch(customActions.FETCH_ALERT_DETAIL, { id: selectedAlertId, view: alertConfig.view });
				updateState({ activeView: 'detail'});
			}
				

			if (data.sort_fields && data.sort_fields.length)
				updateState({
					alertData: newalertData,
					sortColumns: data.sort_fields,
					alertListLoader: false,
					alertDataCount: 50,
					alertDetailLoader: !firstLoadComplete && selectedAlertId ? true : false,
					firstLoadComplete: firstLoadComplete || newalertData && newalertData.length != 0
				});
			else
				updateState({ alertData: newalertData, alertListLoader: false, alertDetailLoader: !firstLoadComplete && selectedAlertId ? true : false, firstLoadComplete: firstLoadComplete || newalertData && newalertData.length != 0 });  //If Active Alerts are not there or record is empty, loader should disappear
		} else
			updateState({ alertData: [], alertListLoader: false });
	}
};

export const fetchAlertRelatedDataSuccess = ({ action: { payload }, dispatch, updateState }) => {
	if (payload.data) {
		const alertRelData = get(payload.data, 'snBcmMap.bcmCrisisMapSchema.getAlertRelatedData');
		if (alertRelData)
			updateState({
				alertRelData,
				responseTasksLoader: false,
				suggestedActionsLoader: false,
				assetsAtRiskLoader: false
			});
	}
};

export const subscribeAlertSuccess = ({ state, action: { payload }, updateState, dispatch }) => {
	const { alertData, selectedAlert, activeView } = state;
	let notificationMsg = "";
	var data = get(payload.data, 'snBcmMap.bcmCrisisMapSchema.subscribeAlert');
	if (data) {
		const sysId = data.id;
		const index = findIndex(alertData, { 'id': sysId });
		const alertRec = alertData[index];
		if (index != -1 && alertRec.subscribed) {
			let alreadyUpdate = false;
			if (activeView == 'detail' && selectedAlert && selectedAlert.subscribed) {
				if (selectedAlert.id == sysId) {
					alreadyUpdate = true;
					updateState([
						{
							path: 'selectedAlert.subscribed',
							value: selectedAlert.subscribed == 'true' ? 'false' : 'true',
							operation: 'set'
						},
						{
							path: `alertData[${index}].subscribed`,
							value: alertRec.subscribed === 'true' ? 'false' : 'true',
							operation: 'set'
						}
					]);
				}
			}
			if (!alreadyUpdate) {
				updateState({
					path: `alertData[${index}].subscribed`,
					value: alertRec.subscribed === 'true' ? 'false' : 'true',
					operation: 'set'
				});
			}

			notificationMsg = {
				status: 'success',
				header: alertRec.subscribed === 'true' ? t(translatedText.MSG_SUCCESS_HEADER_UNSUBSCRIBE) : t(translatedText.MSG_SUCCESS_HEADER_SUBSCRIBE),
				content: alertData[index].title
			};

		}
	} else {
		notificationMsg = {
			status: 'failed',
			header: t(translatedText.MSG_FAILURE),
			content: t(translatedText.MSG_FAILURE_SUBSCRIBE)
		};
	}
	notificationMsg && dispatch(crisisActions.NOTIFICATION_MSG_RECEIVED, { notificationMsg });

};

export const updateAlertStateSuccess = ({ action: { payload }, dispatch, state, updateState }) => {
	let alertData = clone(state.alertData);

	var data = get(payload.data, 'GlideRecord_Mutation.update_sn_fam_alert');
	if (data) {
		const updatedAlertSysId = data.sys_id.value;
		const alertActiveState = data.active.value;

		let index = findIndex(alertData, { 'id': updatedAlertSysId });
		let notificationMsg = {
			status: 'success',
			header: alertActiveState ? t(translatedText.MSG_SUCCESS_ALERT_STATE_REOPEN) : t(translatedText.MSG_SUCCESS_ALERT_STATE_DISMISS),
			content: alertData[index].title
		};
		dispatch(crisisActions.NOTIFICATION_MSG_RECEIVED, { notificationMsg });
		dispatch(mapActions.UPDATE_LAYER, { 'action': 'remove_records', layer: keywords.ALERT_LAYER_ID, collection: keywords.ALERT_COLLECTION_ID, records: [alertData[index]] });
		updateState([
			{
				path: 'alertData',
				operation: 'splice',
				start: index,
				deleteCount: 1
			},
			{
				path: 'activeView',
				value: 'list',
				operation: 'set'
			}
		]);
		alertData.splice(index, 1);
	}
	if (payload.errors && payload.errors.length) {
		console.log('Error - Dismiss Alert failed.', payload.errors);
		let notificationMsg = {
			status: 'failed',
			header: t(translatedText.MSG_FAILURE),
			content: alertActiveState ? t(translatedText.MSG_FAILURE_ALERT_STATE_REOPEN) : t(translatedText.MSG_FAILURE_ALERT_STATE_DISMISS)
		};
		dispatch(crisisActions.NOTIFICATION_MSG_RECEIVED, { notificationMsg });
	}
};

export const updateAlertImpactSuccess = ({ state, updateProperties, updateState, action: { payload }, dispatch }) => {
	updateProperties({ updateAlert: null });
	let newAlert = clone(state.selectedAlert), content = "";
	var data = get(payload.data, 'snBcmMap.bcmCrisisMapSchema.updateAlertImpact');
	let { id, impact_area, impact_radius, impact_type, impact_state, resources, action } = data;
	if (newAlert && newAlert.id == id) {
		newAlert.impact_area = impact_area;
		newAlert.impact_radius = impact_radius;
		newAlert.impact_type = impact_type;
		newAlert.impact_state = impact_state;
		if (action === 'update_area') {
			content = t(translatedText.MSG_SUCCESS_IMPACT_AREA);
		} else if (action === 'update_radius') {
			content = t(translatedText.MSG_SUCCESS_IMPACT_RADIUS);
		} else if (action === 'revert') {
			content = t(translatedText.MSG_SUCCESS_REVERT_AREA);
			dispatch(mapActions.TRIGGER_MARKER_ACTION, { action: 'shape', type: 'revert', layer: keywords.ALERT_LAYER_ID, collection: keywords.ALERT_COLLECTION_ID, record: newAlert.id, recordData: newAlert });
		}

		let notificationMsg = {
			status: 'success',
			header: t(translatedText.MSG_SUCCESS),
			content
		};
		dispatch(crisisActions.NOTIFICATION_MSG_RECEIVED, { notificationMsg });

		updateState([
			{
				path: 'selectedAlert',
				value: { impact_area, impact_radius, impact_type, resources, impact_state },
				operation: 'merge'
			},
			{
				path: 'alertRelData',
				value: { resources },
				operation: 'assign'
			},
			{
				path: 'assetsAtRiskLoader',
				value: false,
				operation: 'set'
			}
		]);
	}
};

export const runAlertActionSuccess = ({ action, state: { alertData, selectedAlert }, updateState, dispatch }) => {
	let updated = false;
	if (action.payload.data && action.payload.data) {
		const data = get(action.payload, 'data.snBcmMap.bcmCrisisMapSchema.runAlertAction');
		if (data && data.status == "success") {
			const header = t(translatedText.MSG_SUCCESS);
			const content = t(translatedText.MSG_SUCCESS_RUN_ALERT);
			let notificationMsg = {
				status: 'success',
				header,
				content
			};
			dispatch(crisisActions.NOTIFICATION_MSG_RECEIVED, { notificationMsg });
			updated = true;
			let index = findIndex(alertData, { 'id': selectedAlert.id });
			updateState(
				[
					{
						path: 'alertRelData.tasks',
						value: data.tasks,
						operation: 'assign'
					},
					{
						path: `alertData[${index}].response_task_count`,
						value: data.tasks.length,
						operation: 'set'
					},
					{
						path: 'responseTasksLoader',
						value: false,
						operation: 'set'
					},
					{
						path: 'suggestedActionsLoader',
						value: false,
						operation: 'set'
					}
				]);
		}
	}
	if (!updated)
		updateState({
			responseTasksLoader: false,
			suggestedActionsLoader: false,
		});
};

