import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-record-html-editor';
import '@servicenow/now-select';
import '@servicenow/now-icon';
import '@servicenow/now-modal';
import '@servicenow/now-record-typeahead-multiple';
import '@servicenow/now-record-field-lookup-connected';
import { t } from 'sn-translate';

const notification_target_url = require('../assets/notification_target.jpg');

const getdropDownItems = (resources) => {
    //dynamically set dropdown records
    let dropDownItems = [{ id: 'all', label: t('All Assets') }];

    resources.map((r) => {
        if (r.contact) {
            let item = {};
            item.label = r.contact;
            item.id = r.config_id;
            dropDownItems.push(item);
        }
    });

    return dropDownItems;
};

export const CommunicationActionModel = ({ state, updateState }) => {
    const { selectedAlert, communicationModal, communicationActionData, adhocUsers, invalidModalInputs, alertRelData: { resources } } = state;
    return <Fragment>
        < now-modal size="lg" headerLabel={t('Notify Stakeholders')} opened={communicationModal == 'start'}
            append-to-payload={{ type: 'communication' }}
            footerActions={[
                { label: t('Next'), variant: 'primary', id: 'next', type: "communication" },
                { label: t('Cancel'), variant: 'secondary', id: 'cancel', type: "communication" }
            ]}
        >
            <div className="target">
                <div className="comm-options-container">
                    <div className="target-img">
                        <img alt={t('Notification image')} src={notification_target_url}></img>
                    </div>
                    <div className="target-audience-container">
                        <h4> {t('Step 1 - Target Audience')} </h4>
                        <div className="target-desc">
                            {t('Take a moment to identify the target audience for this communication.')}
                        </div>
                        {resources.length > 0 ? <div>
                            <now-select search="none" helper-content={t('Please select the contact for the impacted assets')} append-to-payload={{ type: 'assetselect' }} items={getdropDownItems(resources)}
                                label={t('Select an audience')} required={true} selected-item="all">
                            </now-select>
                        </div> : null}

                        <div className="multi-container">
                            <now-record-field-lookup-connected
                                tableName="sys_user"
                                fieldName="name"
                                label={t('Additional audience')}
                                multiSelection={true}
                                description={t('Additional Users to be notified')}
                                additionalQueryString={'active=true^locked_out=false^emailISNOTEMPTY'}
                                displayValue={adhocUsers.displayValue}
                                value={adhocUsers.value}
                                required={!resources.length}
                                invalid={invalidModalInputs.com_users}
                                onValueChange={({ value, displayValue }) => {
                                    updateState({
                                        adhocUsers: {
                                            value,
                                            displayValue
                                        },
                                        communicationActionData: {
                                            subject: communicationActionData.subject,
                                            body: communicationActionData.body,
                                            resource: communicationActionData.resource,
                                            users: value
                                        },
                                        invalidModalInputs: {}
                                    });
                                }}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </now-modal>
        <now-modal size="lg" headerLabel={t('Notify Stakeholders')} opened={communicationModal == 'next'} append-to-payload={{ type: 'communication' }}
            footerActions={[
                { label: t('Send Notification'), variant: 'primary', id: 'submit', type: "communication" },
                { label: t('Back'), variant: 'secondary', id: 'back', type: "communication" }
            ]}
        >
            <div className="email">
                <div className="comm-options-container">
                    <div className="email-push-container">
                        <h4 className="email-push-title" >{t('Step 2 - Draft the Email')}</h4>
                        <div className="email-push-subtitle">
                            {t('Next, configure the contents of the email message. This will define what recipients will see in their inbox.')}
                        </div>
                        <div>
                            <now-input label={t('Email Subject')} className="full-width"
                                name="subject" messages={[]} placeholder="" invalid={invalidModalInputs.com_subject}
                                required={true} step="any" type="text" value={selectedAlert.title}>
                            </now-input>
                            <now-record-html-editor
                                name="email"
                                label={t('Email Body')}
                                required={true}
                                invalid={invalidModalInputs.com_body}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </now-modal>
    </Fragment>
};