
const alertListQuery = `
query ($alertConfig: snBcmMap_bcmCrisisMapSchema_AlertConfig!) {
  snBcmMap {
      bcmCrisisMapSchema {
          getAlertList(alertConfig: $alertConfig) {
            sort_fields{
              label
              id
            }
            records{
            id
            title
            description
            longitude
            latitude
            icon
            active
            subscribed
            can_subscribe
            source
            response_task_count
            fields{
              label
              display_value
            }
            can_write
            severity {
              display
              value
              color
            }
          }
        }
      }
      }
    }
`;

export default alertListQuery;

