import { createCustomElement } from '@servicenow/ui-core';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import styles from './crisis-alert-resize-control.scss';
import { nowActions, crisisActions, mapActions, keywords } from '../../constants';
import { t } from 'sn-translate';
import '@servicenow/now-heading';
import '@servicenow/now-radio-buttons';

export const view = ({ properties: { selectedAlertId } }) => {
	return (
		<div className="alert-resize">
			<div>
				<now-heading label={t('Edit Impacted Area')} level="1" variant="title-secondary"></now-heading>
				<div className="sub-title">{t('Choose how to display the impacted area for this alert')}</div>
				<now-radio-buttons label={t('Display impact area using')} layout="vertical" append-to-payload={{ selectedAlertId }} options={
					[
						{ 'id': 'custom_shape', 'label': t('Custom Shape') },
						{ 'id': 'custom_radius', 'label': t('Custom Radius') }
					]
				}></now-radio-buttons>
			</div>
			<div className="footer">
				<now-button label={t('Cancel')} variant="secondary" append-to-payload={{ type: 'cancel', selectedAlertId }} size="sm"></now-button>
				<div className="space"></div>
				<now-button label={t('Save')} variant="primary" append-to-payload={{ type: 'save', selectedAlertId }} size="sm"></now-button>
			</div>
		</div>
	)
};

export const actionHandlers = {
	[nowActions.NOW_BUTTON_CLICKED]: {
		effect: ({ dispatch, action: { payload: { type, selectedAlertId } } }) => {
			if (type == 'cancel') {
				dispatch(mapActions.TRIGGER_MARKER_ACTION, { action: 'shape', type: 'revert', layer: keywords.ALERT_LAYER_ID, collection: keywords.ALERT_COLLECTION_ID, record: selectedAlertId });
				dispatch(crisisActions.EDIT_MODE_SET, { enable: false });
			} else {
				dispatch(mapActions.TRIGGER_MARKER_ACTION, { action: 'shape', type: 'update', layer: keywords.ALERT_LAYER_ID, collection: keywords.ALERT_COLLECTION_ID, record: selectedAlertId });
				dispatch(crisisActions.EDIT_MODE_SET, { enable: false });
			}
		},
		stopPropagation: true
	},
	[nowActions.NOW_RADIO_BUTTONS_VALUE_SET]: {
		effect: ({ dispatch, action: { payload: { value, selectedAlertId } } }) => {
			if (value == 'custom_shape')
				dispatch(mapActions.TRIGGER_MARKER_ACTION, { action: 'shape', type: 'polygon', layer: keywords.ALERT_LAYER_ID, collection: keywords.ALERT_COLLECTION_ID, record: selectedAlertId });
			else if (value == 'custom_radius')
				dispatch(mapActions.TRIGGER_MARKER_ACTION, { action: 'shape', type: 'circle', layer: keywords.ALERT_LAYER_ID, collection: keywords.ALERT_COLLECTION_ID, record: selectedAlertId });
		},
		stopPropagation: true
	}
};

createCustomElement('crisis-alert-resize-control', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		selectedAlertId: {
			default: ""
		}
	},
	initialState: {
	},
	styles,
	actionHandlers
});