import { customActions, nowActions, mapActions, crisisActions, keywords } from './constants';
import { debounce, findIndex } from 'lodash';
import * as query from './handlers/queryHandler';
import * as data from './handlers/dataHandler';
import { actionTypes } from '@servicenow/ui-core';
import { clone } from 'lodash';
import { t } from 'sn-translate';
const WORKSPACE_CONFIG_ID = '03043934b32300107a6de81816a8dc92';

const _getSortOrder = (alertConfig) => {
	if (alertConfig.sort_fields) {
		for (var f in alertConfig.sort_fields) {
			var colAry = alertConfig.sort_fields[f].split(":");
			if (colAry[0] == alertConfig.sort_by && colAry[1]) {
				if (colAry[1] == 'asc')
					return 'asc';
			}
		}
	}
	return 'desc';
};

const actionHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ state: { properties: { alertConfig } }, dispatch, updateState }) => {
		dispatch(customActions.FETCH_ALERT_LIST, { alertConfig });
	},
	[nowActions.SELECTED_ITEM_SET]: {
		effect: ({ updateState, action }) => {
			updateState({
				tabSelected: action.payload.value
			});
		},
		stopPropagation: true
	},
	[nowActions.NOW_MODAL_OPENED_SET]: {
		effect: ({ updateState, action: { payload } }) => {
			if (payload) {
				resetSuggestedActionModal(updateState, payload.type == 'communication');
			}
		},
		stopPropagation: true
	},
	[nowActions.NOW_BUTTON_BARE_CLICKED]: {
		effect: ({ dispatch, updateState, action: { payload }, state, updateProperties }) => {
			if (payload.type == 'alertsortorder') {
				if (state.alertActiveDropdownState == 'active')
					updateState([
						{
							path: 'activeSortObj.sortOrder',
							value: state.activeSortObj.sortOrder == 'desc' ? 'asc' : 'desc',
							operation: 'assign'
						},
						{
							path: 'alertData',
							value: state.alertData.reverse(),
							operation: 'set'
						},
						{
							path: 'alertDataCount',
							value: 50,
							operation: 'set'
						}
					]);
				else
					updateState([
						{
							path: 'dismissSortObj.sortOrder',
							value: state.dismissSortObj.sortOrder == 'desc' ? 'asc' : 'desc',
							operation: 'assign'
						},
						{
							path: 'alertData',
							value: state.alertData.reverse(),
							operation: 'set'
						},
						{
							path: 'alertDataCount',
							value: 50,
							operation: 'set'
						}
					]
					);

			} else if (payload.type == "backtolist") {
				dispatch(mapActions.TRIGGER_MAP_ACTION, { action: 'reset', type: 'map' });
				updateState({ activeView: 'list', tabSelected: 'res_at_risk', showMore: false });
				dispatch(crisisActions.SET_SELECTED_ALERT_ID, { record: "" });
				dispatch(crisisActions.EDIT_MODE_SET, { enable: false });
				dispatch(nowActions.CONTENT_UPDATED, { params: { 'alert': 'list' } });
			}
		},
		stopPropagation: true
	},
	[nowActions.NOW_CARD_CLICKED]: {
		effect: ({ state: { selectedAlert, properties: { selectedAlertId } }, updateState, updateProperties, action: { payload }, dispatch }) => {
			if (payload.type == 'alertlist' && payload.alert) {
				let alert = payload.alert;
				if (selectedAlertId != alert.id) {
					dispatch(crisisActions.SET_SELECTED_ALERT_ID, { record: alert.id });
					updateState({
						alertDetailLoader: true,
						activeView: 'detail'
					});
				} else {
					//When user dismiss alert from details view and then comes to list view and click on card, correct active state remains in state and selectedAlert needs to be updated as per state.
					if (selectedAlert)
						selectedAlert.active = alert.active == "true" ? true : false;
					dispatch(mapActions.TRIGGER_MARKER_ACTION, { layer: keywords.ALERT_LAYER_ID, collection: keywords.ALERT_COLLECTION_ID, record: alert.id, recordData: selectedAlert });
					updateState({ activeView: 'detail', alertDetailLoader: false });
				}

				dispatch(nowActions.CONTENT_UPDATED, { params: { 'alert': alert.id } });
			} else if (payload.type == 'tasklist' && payload.task) {
				dispatch(nowActions.PREVIEW_RECORD, { table: payload.task.table, sys_id: payload.task.sys_id });
			} else if (payload.type == 'assetlist' && payload.asset) {
				const params = {
					table: payload.asset.table,
					workspaceConfigId: WORKSPACE_CONFIG_ID,
					query: payload.asset.query,
					listTitle: payload.asset.name
				};
				dispatch('ITEM_SELECTED', {
					renderer_type: 'layout',
					params,
					title: t('Assets at Risk')
				});
			}
		},
		stopPropagation: true
	},
	[nowActions.NOW_CARD_HEADER_ACTION_CLICKED]: {
		effect: ({ action: { payload: { action: { id }, type, alert } }, updateState, dispatch, state: { properties: { selectedAlertId } } }) => {
			if (id == 'subscribe_alert' && alert.subscribed)
				dispatch(customActions.SUBSCRIBE_ALERT, { id: alert.id, subscribed: alert.subscribed === 'true' ? false : true });
			else if (id == 'dismiss_alert') {
				dispatch(crisisActions.EDIT_MODE_SET, { enable: false });
				dispatch(customActions.UPDATE_ALERT_STATE, { id: alert.id, active: false });
				if (type != 'alertlist')
					dispatch(mapActions.TRIGGER_MAP_ACTION, { action: 'reset' });
			}
			else if (id == 'open_alert') {
				dispatch(nowActions.PREVIEW_RECORD, { table: alert.table || 'sn_fam_alert', sys_id: alert.id });
			}
			else if (id == 'reopen_alert') {
				dispatch(crisisActions.EDIT_MODE_SET, { enable: false });
				dispatch(customActions.UPDATE_ALERT_STATE, { id: alert.id, active: true });
				if (type != 'alertlist')
					dispatch(mapActions.TRIGGER_MAP_ACTION, { action: 'reset' });
			}
			else if (id == 'edit_mode')
				dispatch(crisisActions.EDIT_MODE_SET, { enable: true });
			else if (id == 'revert_area') {
				updateState({
					assetsAtRiskLoader: true
				});
				dispatch(customActions.UPDATE_ALERT_IMPACT, { id: selectedAlertId, action: 'revert' });
			}
		},
		stopPropagation: true
	},
	[nowActions.NOW_DROPDOWN_ITEM_CLICKED]: {
		effect: ({ updateState, dispatch, action: { payload }, state: { activeSortObj, dismissSortObj, alertActiveDropdownState, properties: { alertConfig } } }) => {
			if (payload.type == 'activeAlerts') {
				if (payload && payload.item.id == 'dismiss') {
					alertConfig.condition = 'active=false',
						alertConfig.sort_by = dismissSortObj.sortField || alertConfig.sort_by;
					alertConfig.sort_order = dismissSortObj.sortOrder || _getSortOrder(alertConfig);
				} else {
					alertConfig.condition = 'active=true';
					alertConfig.sort_by = activeSortObj.sortField || alertConfig.sort_by;
					alertConfig.sort_order = activeSortObj.sortOrder || _getSortOrder(alertConfig);
				}
				updateState({
					alertListLoader: true,
					alertActiveDropdownState: payload.item.id
				});
				dispatch(customActions.FETCH_ALERT_LIST, { alertConfig });
			} else if (payload.type == 'alertSort') {
				alertConfig.sort_by = payload.item.id;
				alertConfig.sort_order = _getSortOrder(alertConfig, payload.item.id);
				if (alertActiveDropdownState == 'active') {
					updateState([
						{
							path: 'activeSortObj.sortField',
							value: payload.item.id,
							operation: 'assign'
						},
						{
							path: 'activeSortObj.sortOrder',
							value: alertConfig.sort_order,
							operation: 'assign'
						},
						{
							path: 'alertListLoader',
							value: true,
							operation: 'set'
						}
					]);
				} else {
					updateState([
						{
							path: 'dismissSortObj.sortField',
							value: payload.item.id,
							operation: 'assign'
						},
						{
							path: 'dismissSortObj.sortOrder',
							value: alertConfig.sort_order,
							operation: 'assign'
						},
						{
							path: 'alertListLoader',
							value: true,
							operation: 'set'
						}
					]);
				}
				dispatch(customActions.FETCH_ALERT_LIST, { alertConfig });
			}

		},
		stopPropagation: true
	},
	[nowActions.NOW_SELECT_SELECTED_ITEM_SET]: {
		effect: ({ updateState, action: { payload } }) => {
			if (payload.type == "assetselect") {
				updateState({
					path: 'communicationActionData.resource',
					value: payload.value || 'all',
					operation: 'set'
				});
			}
		},
		stopPropagation: true
	},
	[nowActions.NOW_INPUT_INVALID_SET]: {
		effect: ({ updateState, action: { payload }, state: { crisisActionData } }) => {
			if (payload.type == "crisis") {
				updateState({
					crisisActionData: {
						resources: crisisActionData.resources,
						short_description: payload.name == 'short_description' ? payload.fieldValue : crisisActionData.short_description,
						description: payload.name == 'description' ? payload.fieldValue : crisisActionData.description,
					},
					invalidModalInputs: {
						cri_short_description: payload.name == 'short_description' ? payload.fieldValue ? null : true : null,
						cri_description: payload.name == 'description' ? payload.fieldValue ? null : true : null
					}
				});
			} else {
				updateState([{
					path: 'communicationActionData.subject',
					value: payload.fieldValue,
					operation: 'set'
				}, {
					path: 'invalidModalInputs.com_subject',
					value: payload.fieldValue ? null : true,
					operation: 'set'
				}]);
			}
		},
		stopPropagation: true
	},
	[nowActions.NOW_RECORD_HTML_EDITOR_VALUE_CHANGED]: {
		effect: ({ updateState, action: { payload } }) => {
			if (payload.name == 'email') {
				updateState([{
					path: 'communicationActionData.body',
					value: payload.value,
					operation: 'set'
				}, {
					path: 'invalidModalInputs.com_body',
					value: payload.value ? null : true,
					operation: 'set'
				}]);
			}
		},
		stopPropagation: true
	},
	[nowActions.NOW_RECORD_HTML_EDITOR_STAGED_VALUE_CHANGED]: {
		effect: ({ updateState, action: { payload } }) => {
			if (payload.name == 'email') {
				updateState([{
					path: 'communicationActionData.body',
					value: payload.value,
					operation: 'set'
				}, {
					path: 'invalidModalInputs.com_body',
					value: payload.value ? null : true,
					operation: 'set'
				}]);
			}
		},
		stopPropagation: true
	},
	[nowActions.NOW_TEXT_LINK_CLICKED]: {
		effect: ({ updateState, action: { payload }, dispatch, state: { selectedAlert, alertRelData: { resources, actions } } }) => {
			if (payload.type == "suggested_action") {
				if (payload.action.action_name == "notify_crisis") {
					updateState({
						communicationModal: 'start',
						communicationActionData: {
							resource: resources && resources.length ? 'all' : '',
							users: null,
							subject: selectedAlert.title,
							body: null
						}
					});
				} else if (payload.action.action_name == "declare_crisis") {
					var resourcesAry = resources ? resources.map(r => r.config_id).join(',') : null;
					updateState({
						crisisModal: 'start',
						crisisActionData: {
							resources: resourcesAry,
							short_description: selectedAlert.title,
							description: null
						}
					});
				} else {

					dispatch(customActions.RUN_ALERT_ACTION, { "action_id": payload.action.id, "alert_id": payload.selectedAlert.id, "inputs": null });
					var actionIndex = findIndex(actions, { id: payload.action.id });
					if (actions[actionIndex].run_once) {
						updateState([
							{
								path: 'alertRelData.actions',
								operation: 'splice',
								start: actionIndex,
								deleteCount: 1
							},
							{
								path: "suggestedActionsLoader",
								value: true,
								operation: 'set'
							},
							{
								path: "responseTasksLoader",
								value: true,
								operation: 'set'
							}
						]);
					} else {
						updateState({
							suggestedActionsLoader: true,
							responseTasksLoader: true
						});
					}
				}
			}
		},
		stopPropagation: true
	},
	[nowActions.NOW_CHECKBOX_CHECKED_SET]: {
		effect: ({ updateState, action: { payload }, state: { crisisActionData: { resources } } }) => {
			if (payload.type == 'crisis') {
				if (resources) {
					var resources = resources.split(',');
					var rid = resources.indexOf(payload.name);
					if (rid > -1)
						resources.splice(rid, 1);
					else
						resources.push(payload.name);
					resources = resources.join(",");
				} else
					resources = payload.name;
				updateState({
					path: 'crisisActionData.resources',
					value: resources,
					operation: 'set',
				});
			}
		},
		stopPropagation: true
	},

	[customActions.ALERT_REFRESH_TRIGGERED]: {
		effect: ({ updateState, dispatch, state: { activeSortObj, dismissSortObj, alertActiveDropdownState, properties: { alertConfig } }, action: { payload: { action: { id } } } }) => {
			if (id == 'alert_refresh') {
				const alertConfigClone = clone(alertConfig);
				if (alertActiveDropdownState == 'active') {
					alertConfigClone.condition = 'active=true';
					alertConfigClone.sort_by = activeSortObj.sortField || alertConfig.sort_by;
					alertConfigClone.sort_order = activeSortObj.sortOrder || _getSortOrder(alertConfig);
				} else {
					alertConfigClone.condition = 'active=false';
					alertConfigClone.sort_by = dismissSortObj.sortField || alertConfig.sort_by;
					alertConfigClone.sort_order = dismissSortObj.sortOrder || _getSortOrder(alertConfig);
				}
				updateState({ alertListLoader: true });
				dispatch(customActions.FETCH_ALERT_LIST, { alertConfig: alertConfigClone });
			}
		},
		stopPropagation: true
	},


	[nowActions.NOW_MODAL_FOOTER_ACTION_CLICKED]: {
		effect: ({ state: { communicationActionData, crisisActionData, alertRelData, selectedAlert, adhocUsers }, updateState, action: { payload: { action } }, dispatch }) => {
			if (action.type == 'communication' || action.type == 'crisis') {
				var modelType = action.type == 'crisis' ? 'crisisModal' : 'communicationModal'
				switch (action.id) {
					case 'next':
						if (action.type == 'communication') {
							if (!alertRelData.resources.length && !adhocUsers.value) {
								updateState(
									{
										path: 'invalidModalInputs.com_users',
										value: true,
										operation: 'set'
									})
								break;
							}
						} else if (action.type == 'crisis' && (!crisisActionData.short_description || !crisisActionData.description)) {
							if (!crisisActionData.short_description && !crisisActionData.description)
								updateState({
									invalidModalInputs: {
										short_description: true,
										description: true
									}
								});
							else
								updateState({
									invalidModalInputs: {
										[crisisActionData.short_description ? 'cri_description' : 'cri_short_description']: true
									}
								});
							break;
						}
						updateState({ [modelType]: 'next', invalidModalInputs: {} });
						break;

					case 'back':
						updateState({ [modelType]: 'start' });
						break;

					case 'cancel':
						resetSuggestedActionModal(updateState, action.type == 'communication')
						break;

					case 'submit':
						{
							let actionIndex;
							let action_id;
							let inputData;

							if (action.type == 'communication') {
								if (communicationActionData && communicationActionData.subject && communicationActionData.body) {
									actionIndex = findIndex(alertRelData.actions, { action_name: 'notify_crisis' });
									action_id = alertRelData.actions[actionIndex].id;
									inputData = communicationActionData;
								} else {
									if (!communicationActionData.subject && !communicationActionData.body)
										updateState({
											invalidModalInputs: {
												com_subject: true,
												com_body: true
											}
										});
									else
										updateState({
											invalidModalInputs: {
												[communicationActionData.subject ? 'com_body' : 'com_subject']: true
											}
										});
									break;
								}
							} else {
								if (crisisActionData && crisisActionData.short_description && crisisActionData.description) {
									actionIndex = findIndex(alertRelData.actions, { action_name: 'declare_crisis' });
									action_id = alertRelData.actions[actionIndex].id;
									inputData = crisisActionData;
								} else
									break;
							}
							updateState({
								[modelType]: null, suggestedActionsLoader: true, responseTasksLoader: true, invalidModalInputs: {},
								adhocUsers: {
									displayValue: [],
									value: ''
								},
								communicationActionData: {
									resource: '',
									users: null,
									subject: null,
									body: null
								},
								crisisActionData: {
									resources: null,
									short_description: null,
									description: null
								}
							});
							var inputs = [];
							for (var i in inputData)
								inputs.push({
									name: i,
									value: inputData[i]
								});
							dispatch(customActions.RUN_ALERT_ACTION, { action_id, alert_id: selectedAlert.id, inputs });
							if (alertRelData.actions[actionIndex].run_once) {
								updateState({
									path: 'alertRelData.actions',
									operation: 'splice',
									start: actionIndex,
									deleteCount: 1
								});
							}
						}
						break;

					default:
						break;
				}
			}
		},
		stopPropagation: true
	},
	[nowActions.INPUT]: {
		effect: debounce(({ updateState, action }) => {
			if (action.payload.type !== 'search')
				return;
			if (action.payload.fieldValue == "" || action.payload.fieldValue.length > 2)
				updateState({
					searchTerm: action.payload.fieldValue
				});
		}, 500),
		stopPropagation: true
	},
	[customActions.SELECTED_ALERT_CHANGED]: {
		effect: ({ state: { properties: { alertConfig } }, updateState, dispatch, action: { payload: { alertId } } }) => {
			if (alertId) {
				updateState({
					alertDetailLoader: true,
					responseTasksLoader: true,
					suggestedActionsLoader: true,
					assetsAtRiskLoader: true,
					activeView: 'detail'
				});
				dispatch(customActions.FETCH_ALERT_DETAIL, { id: alertId, view: alertConfig.view });
			}
		},
		stopPropagation: true
	},
	[customActions.INITIATE_UPDATE_ALERT_IMPACT]: {
		effect: ({ action: { payload }, updateState, dispatch }) => {
			updateState({
				assetsAtRiskLoader: true
			});
			dispatch(customActions.UPDATE_ALERT_IMPACT, payload);
		},
		stopPropagation: true
	},
	[customActions.FETCH_ALERT_DETAIL_SUCCESS]: {
		effect: data.fetchAlertDetailSuccess,
		stopPropagation: true
	},
	[customActions.FETCH_ALERT_DETAIL_FAILURE]: {
		effect: data.fetchAlertDetailFailure,
		stopPropagation: true
	},
	[customActions.FETCH_ALERT_RELATED_DATA_SUCCESS]: {
		effect: data.fetchAlertRelatedDataSuccess,
		stopPropagation: true
	},
	[customActions.FETCH_ALERT_RELATED_DATA_FAILURE]: {
		effect: data.fetchAlertRelatedDataFailure,
		stopPropagation: true
	},
	[customActions.UPDATE_ALERT_STATE_SUCCESS]: {
		effect: data.updateAlertStateSuccess,
		stopPropagation: true
	},
	[customActions.UPDATE_ALERT_STATE_FAILURE]: {
		effect: data.updateAlertStateFailure,
		stopPropagation: true
	},
	[customActions.FETCH_ALERT_LIST_SUCCESS]: {
		effect: data.fetchAlertListSuccess,
		stopPropagation: true
	},
	[customActions.FETCH_ALERT_LIST_FAILURE]: {
		effect: data.fetchAlertListFailure,
		stopPropagation: true
	},
	[customActions.SUBSCRIBE_ALERT_SUCCESS]: {
		effect: data.subscribeAlertSuccess,
		stopPropagation: true
	},
	[customActions.SUBSCRIBE_ALERT_FAILURE]: {
		effect: data.subscribeAlertFailure,
		stopPropagation: true
	},
	[customActions.UPDATE_ALERT_IMPACT_SUCCESS]: {
		effect: data.updateAlertImpactSuccess,
		stopPropagation: true
	},
	[customActions.UPDATE_ALERT_IMPACT_FAILURE]: {
		effect: data.updateAlertImpactFailure,
		stopPropagation: true
	},
	[customActions.RUN_ALERT_ACTION_SUCCESS]: {
		effect: data.runAlertActionSuccess,
		stopPropagation: true
	},
	[customActions.RUN_ALERT_ACTION_FAILURE]: {
		effect: data.runAlertActionFailure,
		stopPropagation: true
	},
	[customActions.UPDATE_ALERT_STATE]: query.updateAlertStateEffect,
	[customActions.FETCH_ALERT_DETAIL]: query.fetchAlertDetail,
	[customActions.FETCH_ALERT_LIST]: query.fetchAlertList,
	[customActions.SUBSCRIBE_ALERT]: query.subscribeAlertEffect,
	[customActions.UPDATE_ALERT_IMPACT]: query.updateAlertImpact,
	[customActions.FETCH_ALERT_RELATED_DATA]: query.fetchAlertRelatedData,
	[customActions.RUN_ALERT_ACTION]: query.runAlertAction
};

export {
	actionHandlers
};

const resetSuggestedActionModal = (updateState, isCommunication) => {
	if (isCommunication)
		updateState({
			communicationModal: null,
			invalidModalInputs: {},
			adhocUsers: {
				displayValue: [],
				value: ''
			},
			communicationActionData: {
				resource: '',
				users: null,
				subject: null,
				body: null
			}
		});
	else {
		updateState({
			crisisModal: null,
			invalidModalInputs: {},
			crisisActionData: {
				resources: null,
				short_description: null,
				description: null
			}
		});
	}
};