const runAlertActionQuery = `
mutation ($action_id: String!, $alert_id: String!, $inputs: [snBcmMap_bcmCrisisMapSchema_ActionInput]) {
  snBcmMap {
    bcmCrisisMapSchema {
        runAlertAction(action_id:$action_id, alert_id:$alert_id, inputs:$inputs) {
         status
         tasks{
          table
          sys_id
          number
          short_description
          fields {
            name
            value
            display_value
            label
            reference
          }
        }
        }
      }
    }
  }  
`;
export default runAlertActionQuery;