import { createCustomElement } from '@servicenow/ui-core';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import style from './crisis-alert-view-control.scss';
import view from './view';
import { customActions, nowActions } from './constants';
import { actions, actionHandlers } from './actions';

createCustomElement('crisis-alert-view-control', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		alerts: {
			default: []
		},
		events: {
			default: []
		},
		alertConfig: {
			default: {}
		},
		editMode: {
			default: false
		},
		mapLayers: {
			default: {}
		},
		selectedAlertId: {
			default: '',
			onChange(currentValue, previousValue, dispatch) {
				if (currentValue && currentValue != previousValue)
					dispatch(customActions.SELECTED_ALERT_CHANGED, { alertId: currentValue });
			}
		},
		updatedArea: {
			default: {},
			onChange(currentValue, previousValue, dispatch) {
				if (currentValue && currentValue.id && currentValue.value) {
					let obj = { id: currentValue.id };
					if (currentValue.shape == 'polygon') {
						obj.impact_area = JSON.stringify(currentValue.value);
						obj.action = "update_area";
					} else {
						obj.impact_radius = currentValue.value; // TODO: where is value coming from?
						obj.action = "update_radius";
					}

					dispatch(customActions.INITIATE_UPDATE_ALERT_IMPACT, obj);
				}
			}
		},
		mapProperties: {
			default: []
		}
	},
	initialState: {
		tabSelected: 'res_at_risk',
		alertData: null,
		activeView: 'list',
		selectedItem: {},
		selectedAlert: null,
		alertDetailLoader: false,
		alertListLoader: true,
		searchTerm: '',
		alertRelData: {},
		alertDataCount: 50,
		firstLoadComplete: false,
		assetsAtRiskLoader: true,
		suggestedActionsLoader: true,
		responseTasksLoader: true,
		showMore: false,
		executedActions: [],
		alertActiveDropdownState: 'active',
		activeSortObj: {
			sortOrder: 'asc',
			sortField: 'severity'
		},
		dismissSortObj: {
			sortOrder: 'desc',
			sortField: 'sys_updated_on'
		},
		sortColumns: [],
		communicationModal: '',
		crisisModal: '',
		communicationActionData: {
			resource: null,
			users: null,
			subject: null,
			body: null
		},
		crisisActionData: {
			resources: null,
			short_description: null,
			description: null
		},
		adhocUsers: {
			displayValue: [],
			value: ''
		},
		invalidModalInputs: {}
	},
	styles: style,
	actions,
	actionHandlers,
	//transformState,
	eventHandlers: [{
		events: ['click'],
		effect({ dispatch, action: { payload: { event } } }) {
			const path = event.composedPath() || event.path || [];

			if (path.length === 0 && path[0].tagName != 'A' && path[2].tagName != 'NOW-TEXT-LINK')
				return true;

			let anchor = path[0];
			if (!anchor || !anchor.getAttribute('href'))
				return;

			const url = anchor.getAttribute('href');
			const cleanedUrl = url.length > 0 && url[0] === '/' ? url.substring(1) : url;

			if (cleanedUrl.indexOf('&alert_link') == -1)
				return;

			if (event.ctrlKey || event.shiftKey || event.metaKey || (event.button && event.button === 1))
				return;

			const table = cleanedUrl.split('.')[0];
			const sys_id = cleanedUrl.split('sys_id=')[1].split('&')[0];

			if (!table || !sys_id)
				return;

			dispatch(nowActions.PREVIEW_RECORD, {
				table,
				sys_id
			});

			event.stopPropagation();
			event.preventDefault();
		}
	}]
});